import { NgModule } from "@angular/core";
import { PreloadAllModules, RouterModule, Routes } from "@angular/router";
import { AuthguardService } from "./services/authguard.service";

const routes: Routes = [
  {
    path: "",
    redirectTo: "login",
    pathMatch: "full",
  },
  {
    path: "login",
    loadChildren: () =>
      import("./pages/login/login.module").then((m) => m.LoginPageModule),
  },
  {
    path: "home",
    loadChildren: () =>
      import("./pages/home/home.module").then((m) => m.HomePageModule),
    canActivate: [AuthguardService],
  },
  {
    path: "content",
    loadChildren: () =>
      import("./pages/content/content.module").then((m) => m.ContentPageModule),
    canActivate: [AuthguardService],
  },
  {
    path: "slide",
    loadChildren: () =>
      import("./pages/slide/slide.module").then((m) => m.SlidePageModule),
    canActivate: [AuthguardService],
  },
  {
    path: "lyric",
    loadChildren: () =>
      import("./pages/lyric/lyric.module").then((m) => m.LyricPageModule),
    canActivate: [AuthguardService],
  },
  {
    path: "drum",
    loadChildren: () =>
      import("./pages/drum/drum.module").then((m) => m.DrumPageModule),
    canActivate: [AuthguardService],
  },
  {
    path: "embed",
    loadChildren: () =>
      import("./pages/embed/embed.module").then((m) => m.EmbedPageModule),
    canActivate: [AuthguardService],
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules }),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {}
